html {
  height: 100%;
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn__social--list {
  width: 100%;
  margin: 24px 0;
}

button:active,
button:focus {
  outline: none;
}

button::-moz-focus-inner {
  border: 0;
}

/* minimal
*******************************************************************************/
.minimal {
  display: inline-flex;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid #bbb;
  letter-spacing: 1px;
  font-size: 14px;
  font-weight: 300;
  width: 150px;
  height: 45px;
  transition: all 0.2s ease 0s;
}
  .minimal:hover {
    background: #d9d9d9;
    color: #222;
    cursor: pointer;
  }

  .minimal:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }

/* minimal indent (inspired by okcupid iphone interface)
*******************************************************************************/
.minimal-indent {
  background-color: #f3f3f3;
  border-radius: 5px;
  box-shadow: inset 0 1px 0 0 white, 0 1px 0 0 #d5d5d5, 0 -1px 2px 1px #efefef;
  color: #666;
  text-shadow: 0 1px 1px #fff;
}
  .minimal-indent:hover {
    background-color: #e5e5e5;
    box-shadow: inset 0 1px 0 0 #f2f2f2, 0 1px 0 0 #c9c9c9, 0 -1px 2px 1px #e3e3e3;
  }


main {
  margin: 12px;
}

.hidden {
  display: none;
}

.text--center {
  text-align: center;
}

.root__title {
  font-size: 24px;
  font-weight: 300;
}
.mentor__title {
  font-size: 22px;
  font-weight: 500;
  margin: 0;
}

.mentor__data {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 12px;
  border-bottom: 2px solid #f56433;
}

.mentor__data-tooltip {
  font-size: 14px;
  margin-left: 22px;
}
.task {
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #fff;
}

.task__title {
  padding: 5px;
}

.task__specification {
  color: #8d8173;
  text-decoration: none;
}

.task__specification:hover {
  opacity: 0.7;
}

.task__process {
  text-align: center;
  height: 20px;
  min-width: 40%;
  position: relative;
}

.task__process::before {
  content: '';
  position: absolute;
  left: 0;
  width: 0;
	height: 0;
	border-top: 10px solid transparent;
	border-left: 5px solid #e1e3e0;
	border-bottom: 10px solid transparent
}

.task__process_checked {
  color: #fff;
  background: #50c878;
}

.task__process_todo {
  color: #fff;
  background: #999;
}

.task__process_inprocess {
  color: #8d8173;
  background: #ffe5b4;
}

.task__process_tocheck {
  color: #fff;
  background: #f56433;
}

.task__process_nochecked {
  color: #fff;
  background: #960018;
}

.task__process_tooltip {
  display: none;
  padding: 4px 6px;
  width: 130px;
}

.task__process:hover .task__process_tooltip {
  display: inline;
  font-size: 12px;
  z-index: 1;
  position : absolute;
  right: 20px;
  bottom: 20px;
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid #ccc;
  border-radius: 5px;
  color: #8d8173;
}
.students__data {
  margin: 10px 5px;
  color: #8d8173;
  font-weight: 300;
  background: #e1e3e0;
  border-radius: 0 18px;
  width: 24%;
  position: relative;
}

@media screen and (max-width: 768px) {
  .students__data {
    width: 100%;
  }
}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px)  {
  .students__data {
    width: 48%;
  }
}

.students__title {
  margin: 10px 5px;
  font-size: 16px;
  font-weight: 500;
}

.students__github {
  position: absolute;
  right: 5px;
  top: 10px;
}

.students__github-link {
  text-decoration: none;
  margin: 0 5px;
  color: #000;
}

.students__github-link:hover {
  opacity: 0.8;
}

.private {
  color: #ca002a;
}
.find-form {
  display: flex;
  justify-content: center;
  width: 100%;
}

.select-form {
  width: 500px;
}
.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #cef;
  border-color: #cef transparent #cef transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
section {
  text-align: left;
  padding: 10px;
  }

.mentor {
  padding: 12px 10px;
